/*! modern-normalize v1.1.0 | MIT License | https://github.com/sindresorhus/modern-normalize */
*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  color: inherit;
  height: 0;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted buttontext;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button {
  height: auto;
}

::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --main-title-color: #fff;
  --description-header-hero: #ffffffb3;
  --second-title-color: #6b6b6b;
  --second-main-text-color: #1a1a1a;
  --main-red-color: #e53e44;
  --background-origin: #fff;
  --background-footer: #1a1a1a;
  --background-pages: #f0f0f0;
  --red-hover-color: #e53e4480;
}

body {
  font-family: Inter, sans-serif;
}

p, h1, h2, h3, h4, h5, h6, ul, input {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
}

.visually-hidden {
  white-space: nowrap;
  clip-path: inset(50%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 3.7%;
  position: relative;
}

@media screen and (width >= 1280px) {
  .container {
    padding: 0 6.7%;
  }
}

.test {
  font-family: Raleway, sans-serif;
}

.section {
  background-color: var(--background-pages);
  padding-top: 30px;
  padding-bottom: 30px;
}

@media screen and (width >= 768px) {
  .section {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}

@media screen and (width >= 1280px) {
  .section {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

.header {
  width: 100%;
  padding-top: 70px;
  padding-bottom: 60px;
}

@media screen and (width >= 768px) {
  .header {
    padding-top: 30px;
  }
}

.home-header {
  background-image: none;
  position: absolute;
}

.other-page-header {
  background-color: #000000b3;
  background-image: linear-gradient(#000000b3, #000000b3), url("Hero.63d52fec.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 70px;
  position: relative;
}

.header__container {
  align-items: center;
}

@media screen and (width >= 768px) {
  .header__container {
    align-content: center;
    display: flex;
  }
}

@media screen and (width >= 1280px) {
  .header__container {
    justify-content: space-between;
    align-items: center;
    display: flex;
    position: relative;
  }
}

.logo__container {
  justify-content: center;
  margin-bottom: 20px;
  display: flex;
}

@media screen and (width >= 768px) {
  .logo__container {
    margin-bottom: 0;
  }
}

@media screen and (width >= 1280px) {
  .logo__container {
    flex: 1;
    justify-content: flex-start;
    display: flex;
  }
}

.logo__link {
  color: var(--main-title-color);
  text-align: center;
  text-decoration: none;
  display: block;
}

@media screen and (width >= 768px) {
  .logo__link {
    text-align: start;
  }
}

.logo {
  margin: 0;
  padding: 0;
  font-size: 24px;
  font-weight: 600;
  line-height: 1.2;
}

@media screen and (width >= 1280px) {
  .logo {
    font-size: 25px;
  }
}

.logo__text {
  color: var(--description-header-hero);
  font-size: 16px;
  font-weight: 400;
}

@media screen and (width >= 768px) {
  .contact__container {
    align-items: center;
    margin-left: auto;
    padding-right: 50px;
    display: flex;
  }
}

@media screen and (width >= 1280px) {
  .contact__container {
    flex: 1;
    justify-content: flex-end;
    padding-right: 0;
