  }
}

.contact__tell {
  color: var(--main-title-color);
  text-align: center;
  margin: 0 0 9px;
  padding: 0;
  font-size: 22px;
  font-weight: 500;
  line-height: 1.2;
  display: block;
}

@media screen and (width >= 768px) {
  .contact__tell {
    text-align: start;
  }
}

.contact__time {
  color: var(--description-header-hero);
  text-align: center;
  margin: 0;
  padding: 0;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.2;
  display: block;
}

@media screen and (width >= 768px) {
  .contact__time {
    text-align: start;
  }
}

.header-menu__open {
  background-color: var(--main-red-color);
  border: none;
  border-bottom-left-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 52px;
  height: 52px;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
}

@media screen and (width >= 768px) {
  .header-menu__open {
    width: 54px;
    height: 54px;
  }
}

@media screen and (width >= 1280px) {
  .header-menu__open {
    display: none;
  }
}

.burger {
  z-index: 1000;
  background-color: #f0f0f0;
  width: 280px;
  height: 100vh;
  padding: 50px 30px;
  transition: right .3s;
  position: fixed;
  top: 0;
  right: -280px;
  overflow-y: auto;
}

.burger.open {
  right: 0;
}

@media screen and (width >= 768px) {
  .burger {
    width: 400px;
    right: -400px;
  }
}

@media screen and (width >= 1280px) {
  .burger {
    background-color: #0000;
    align-items: start;
    width: 40%;
    height: 100px;
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    position: absolute;
    left: 50%;
    overflow-y: hidden;
    transform: translateX(-50%);
  }
}

.header-menu__close {
  background-color: #0000;
  border: none;
  width: 50px;
  height: 50px;
  position: absolute;
  top: 0;
  right: 0;
}

@media screen and (width >= 1280px) {
  .header-menu__close {
    display: none;
  }

  .site-nav__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
  }

  .site-nav__item {
    width: auto;
    padding-right: 25px;
  }
}

.site-nav__item-container {
  align-items: center;
  display: flex;
}

.site-nav__link {
  color: var(--second-main-text-color);
  white-space: nowrap;
  text-overflow: ellipsis;
  padding: 15px 0;
  font-size: 18px;
  font-weight: 600;
  display: block;
  overflow: hidden;
}

.site-nav__link:hover {
  color: var(--main-red-color);
  transition: all .3s;
}

@media screen and (width >= 1280px) {
  .site-nav__link {
    color: var(--description-header-hero);
    flex-wrap: nowrap;
    width: auto;
    font-weight: 500;
  }

  .site-nav__link:hover {
    color: var(--main-title-color);
    transition: all .3s;
  }

  .cls-1 {
    fill: var(--description-header-hero);
  }
}

.site-nav__btn {
  background-color: #0000;
  border: none;
  width: 27px;
  height: 27px;
}

.site-nav__dropdown {
  width: 100%;
  max-height: 0;
  transition: max-height .3s ease-out, opacity .3s ease-out;
  overflow: hidden;
}

.site-nav__dropdown.open {
  max-height: 200px;
}

@media screen and (width >= 1280px) {
  .site-nav__dropdown {
    position: absolute;
  }
}

.site-nav__item-services {
  padding: 0 0 0 10px;
}

.site-nav__link-services {
  color: var(--second-title-color);
  padding: 15px 20px;
  font-size: 16px;
  font-weight: 500;
  display: block;
}

.site-nav__link-services:hover {
  background-color: var(--background-origin);
  color: var(--main-red-color);
}

@media screen and (width >= 1280px) {
  .site-nav {
    display: flex;
  }

  .more-menu__container {
    justify-content: center;
    align-items: center;
    display: flex;
  }
}

.more-menu__btn {
  display: none;
}

@media screen and (width >= 1280px) {
  .more-menu__btn {
    cursor: pointer;
    color: var(--main-title-color);
    border: 1px solid;
    border-radius: 50%;
    justify-content: center;
    width: 35px;
    height: 35px;
    padding-top: 3px;
    font-size: 16px;
    font-weight: 600;
    display: flex;
  }

  .more-menu__list {
    border-width: 1px;
    border-color: #000;
    width: 35px;
    height: 35px;
    display: none;
  }
}

.hero {
  background-color: #000000b3;
  background-image: linear-gradient(#000000b3, #000000b3), url("Hero.63d52fec.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  padding-bottom: 50px;
}

.hero__content {
  padding: 250px 5% 0;
}

.hero__title {
  color: var(--main-title-color);
  text-align: center;
  margin-bottom: 10px;
  font-size: 30px;
  font-weight: 600;
  line-height: 36px;
}

.hero__text {
  color: var(--description-header-hero);
  text-align: center;
  margin-bottom: 26px;
  font-size: 15px;
  line-height: 22.5px;
}

.hero__btn {
  background-color: var(--main-red-color);
  color: var(--main-title-color);
  text-align: center;
  border: none;
  border-radius: 260px;
  justify-content: center;
  width: 100%;
  margin-bottom: 30px;
  padding: 28px 0;
  font-size: 16px;
  font-weight: 600;
  display: flex;
}

.hero__btn:hover {
  box-shadow: var(--main-red-color) 0 0 0 2px inset;
  background-color: #0000;
  transition: all .3s;
}

.hero__nav {
  grid-row-gap: 10px;
  grid-template-columns: 1fr;
  padding: 0;
  list-style: none;
  display: grid;
}

.hero__nav-item {
  align-items: center;
  display: flex;
}

.hero__nav-circle {
  background-color: var(--main-title-color);
  border-radius: 50%;
  width: 12px;
  height: 12px;
}

.hero__nav-line {
  background-color: var(--main-title-color);
  border-radius: 50%;
  width: 6%;
  height: 1px;
}

.hero__nav-link {
  color: var(--main-title-color);
  border-radius: 16px;
  align-items: center;
  width: 100%;
  height: 75.5px;
  padding-left: 24px;
  font-size: 22px;
  font-weight: 600;
  line-height: 27.5px;
  text-decoration: none;
  display: flex;
  box-shadow: inset 0 0 0 1px #fff6;
}

.hero__nav-link:hover {
  background-color: var(--main-title-color);
  color: var(--second-main-text-color);
  transition: all .3s;
}

.breadcrumb {
  font-family: Inter, sans-serif;
  font-size: 15px;
}

.breadcrumb ul {
  color: var(--second-title-color);
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.breadcrumb li:not(:last-child):after {
  content: "/";
  margin: 0 .25rem;
  display: inline-block;
}

.breadcrumb li:not(:last-child) > a {
  color: #008efc;
  text-decoration: underline;
}

.breadcrumb li:not(:last-child) > a:hover, .breadcrumb li:not(:last-child) > a:focus {
  text-decoration: none;
}

.breadcrumb li:last-child > a {
  color: var(--second-title-color);
  pointer-events: none;
  cursor: default;
}

.footer {
  background-color: var(--background-footer);
  text-align: center;
  color: var(--main-title-color);
  font-family: Inter, sans-serif;
}

.footer__content {
