}

@media screen and (width >= 480px) {
  .footer__mail {
    font-size: 22px;
  }
}

@media screen and (width >= 768px) {
  .footer__mail {
    font-size: 24px;
  }
}

@media screen and (width >= 1280px) {
  .footer__mail {
    font-size: 2vw;
  }
}

.footer__email, .footer .contacts {
