  font-weight: 600;
}

@media screen and (width >= 480px) {
  .testimonials__title {
    margin: 20px 0 10px;
    font-size: 30px;
  }
}

@media screen and (width >= 768px) {
  .testimonials__title {
    margin: 30px 0 20px;
    font-size: 40px;
  }
