.modal--second {
  z-index: 1;
  background-color: #0006;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.modal__content--second {
  text-align: center;
  background-color: #fff;
  border: 1px solid #888;
  border-radius: 20px;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  width: 280px;
  margin: 15% auto;
  padding: 30px 24px;
  display: flex;
  position: relative;
  overflow: hidden;
}

@media screen and (width >= 768px) {
  .modal__content--second {
    width: 380px;
    padding: 60px 32px 40px;
  }
}

@media screen and (width >= 1280px) {
  .modal__content--second {
    width: 560px;
    padding: 60px;
  }
}

.modal__close--second {
  text-align: center;
  fill: var(--second-title-color);
  width: 64px;
  height: 64px;
  padding: 10px;
  font-weight: bold;
  position: absolute;
  top: 0;
  right: 0;
}

.modal__close--second:hover, .modal__close--second:focus {
  cursor: pointer;
  text-decoration: none;
}

.modal__header {
  font-weight: 600;
  line-height: 120%;
}

@media screen and (width >= 480px) {
  .modal__header {
    font-size: 22px;
  }
}

@media screen and (width >= 768px) {
  .modal__header {
    font-size: 24px;
  }
}

@media screen and (width >= 1280px) {
  .modal__header {
    font-size: 2vw;
  }
}

.modal__subheader {
  color: var(--second-title-color);
  margin-top: 5px;
  margin-bottom: 15px;
  font-size: 15px;
}

@media screen and (width >= 1280px) {
  .modal__subheader {
    margin-top: 14px;
    margin-bottom: 25px;
    font-size: 1.7vw;
  }
}

@media screen and (width >= 768px) {
  .modal__subheader {
    margin-top: 8px;
